import { sendList } from "../chrome/utils";
import { getVideoObject } from "../utils";
import { VideoType } from "../types/MediaTypes";
import { ActionType, ChromeMessageType } from "../background/actions/types";

const searchVideoInfo = function () {
    const url = window.location.href;

    if(url.split('/video/').length === 2) {
        chrome.runtime.sendMessage<ChromeMessageType>({
            action: ActionType.TikTok,
            url,
        })
    }
};

searchVideoInfo();

chrome.runtime.onMessage.addListener((message: ChromeMessageType) => {
  if (message.action === "pageUpdated") {
      searchVideoInfo();
  }
  if(message.action === ActionType.TikTokResponse) {
      const list: VideoType[] = [];
      getVideoObject(message.data.title, message.data.url, message.data.headers).then(obj => {
          list.push(obj);
          sendList({
              list,
              header: message.data.title,
              id: window.location.href
          });
      });
  }
});